<template>
    <div class="hot">
        <my-scroll class="scroll">
            <div>
                <div class="banner">
                    <div class="rank"></div>
                </div>
                <ul>
                    <li @click="play(item.id)" class="item" v-for="(item, index) in hot" :key="index">
                        <div class="rank">
                            <span :style="'color:'+((index+1)<4?'red':'')">{{index+1}}</span>
                        </div>
                        <div class="song">
                            <div class="name">
                                <span>{{item.name.slice(0,15)}}</span>
                            </div>
                            <div class="author">
                                <span>{{item.al.name}}</span>&nbsp;-
                                <span>{{item.ar[0].name}}</span>
                            </div>
                        </div>
                        <i class="iconfont icon-iconset0481"></i>
                    </li>
                </ul>
            </div>
        </my-scroll>
    </div>
</template>

<script>
    import myScroll from '@/base/scroll/scroll'
    import { getHotSongs} from '@/api/hot'
    export default {
        data(){
            return {
                hot: []
            }
        },
        methods:{
            play(id){
                this.$router.push({path: 'song',query:{id:id}})
            }
        },
        created(){
            getHotSongs().then(res => {
                this.hot = res.tracks.slice(0,15)
            })
        },
        mounted() {
            
        },
        components:{
            myScroll
        }
    }
</script>
<style lang="less" scoped>
    @import "../../common/style/variable.less";
    .hot {
        position: absolute;
        width: 100%;
        top: 88px;
        left: 0;
        bottom: 0;
        .scroll {
            height: 100%;
            overflow: hidden;
            .banner {
                width: 100%;
                position: relative;
                padding-top: 38.9%;
                overflow: hidden;
                background: url('./hot_music_bg_2x.jpg') no-repeat;
                background-size: contain;
                .rank {
                    position: absolute;
                    left: 30px;
                    top: 30px;
                    width: 142px;
                    height: 67px;
                    background-image: url('./index_icon_2x.png');
                    background-size: 166px 97px;
                    background-position: -24px -30px;
                }
            }
            ul {
                display: flex;
                flex-wrap: wrap;
                li {
                    width: 100%;
                    height: 40px;
                    flex: 1 1 auto;
                    padding-left: 10px;
                    padding-top: 10px;
                    position: relative;
                    display: flex;
                    .rank {
                        height: 100%;
                        line-height: 40px;
                        width: 30px;
                        text-align: center;
                    }
                    .song {
                        padding-left: 5px;
                        .name {
                            span {
                                font-size: @font-size-xxl;
                            }
                        }
                        .author {
                            padding-top: 5px;         
                            span {
                                color: @color-text-g;
                                font-size: @font-size-m;
                            }
                        }
                    }
                    .iconfont {
                        position: absolute;
                        right: 10px;
                        top: 5px;
                        font-size: @font-size-big;
                    }
                }
            }
        }
    }
</style>